<template>
	<view class="box">
		<template v-if="data_list.length==0">
			<view class="shop-empty" style="z-index: 99; top: 50px;">
				<image lazy-load class="empty-icon" src="../../static/images/mescroll-empty.png" mode="aspectFill"></image>
				<view class="empty-tip">
					~ 暂无相关数据 ~
				</view>
			</view>
		</template>
		<view v-else class="car-list" v-for="(item,k) in data_list" :key="k">
			<view class="shop-name">
				<view class="iconfont iconlujing" @tap="checkMerchant(k)" :class="{'iconxuanzhong':item.merchant_check}"></view>
				<view class="iconfont icondianpu1"></view>
				<view class="name">{{item.merchantShopName}}</view>
			</view>
			<template v-for="(val,a) in item.myShopingSkuList">
				<uni-swipe-action :key="a">
					<uni-swipe-action-item :options="options" @click="bindClick(val.seqId,k,a)">
						<view class="car-box">
							<view class="iconfont iconlujing" :class="{'iconxuanzhong':val.sku_check}" @tap="checkSku(k,a)"></view>
							<view class="car-box-main">
								<navigator :url="'/pages/goods/detail?seqId='+val.spuSeqId+'&merchantShopId='+item.merchantShopId">
									<image lazy-load class="img scale11" :src="val.skuUrl" mode="aspectFill"></image>
								</navigator>
								<view class="right">
									<navigator :url="'/pages/goods/detail?seqId='+val.spuSeqId+'&merchantShopId='+item.merchantShopId">
										<view class="title">{{val.skuTitle}}</view>
										<view class="tips">{{val.skuSpecs}}</view>
									</navigator>
									<view class="price-box">
										<view class="price font-bold">￥{{val.effectivePrice | formatNum}}</view>
										<add-reduce :num="val.qty" @getNum="getQty($event,k,a)"></add-reduce>
									</view>
								</view>
							</view>
						</view>
					</uni-swipe-action-item>
				</uni-swipe-action>
			</template>
		</view>

		<view class="blank"></view>
		<view class="fix-box">
			<view class="left" @tap="checkAll">
				<view class="iconfont iconlujing" :class="{'iconxuanzhong':check_all}"></view>
				全选
			</view>
			<view class="right">
				<view class="total-price">
					<text class="tips">不含运费</text>合计：<text class="money font-bold">￥{{all_price | formatNum}}</text>
				</view>
				<view class="bg-btn" @tap="introOrder">
					结算
				</view>
			</view>

		</view>
	</view>
</template>

<script>
	import uniSwipeAction from './components/uni-swipe-action/uni-swipe-action.vue'
	import uniSwipeActionItem from './components/uni-swipe-action-item/uni-swipe-action-item.vue'
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue"
	import addReduce from './components/add-reduce.vue'
	import Vue from 'vue'
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				upOption: {
					textNoMore: "没有更多数据"
				},
				data_list: [],
				all_check: false,
				shop_num:5,
				options: [{
					text: '删除',
					style: {
						backgroundColor: '#e20d0d'
					}
				}],
			}
		},
		
		components: {
			addReduce,
			MescrollBody,
			uniSwipeAction,
			uniSwipeActionItem
		},
		onPullDownRefresh() {
			this.loadList();
			 setTimeout(function () {
			            uni.stopPullDownRefresh();
			        }, 1000);
		},
		onLoad() {
			this.loadList();
			uni.showLoading({
				title: '加载中'
			});
			setTimeout(function(){
				uni.hideLoading()
			},600)
		},
		methods: {
			//点击提交到结算页面 生成临时订单
			introOrder() {
				let time = new Date().getTime();
				let myShopingShopList = [];
				let oneShopingShopObj = [];
				let myShopingSkuList = [];
				let myShopingSkuObj = {};
				for (let val of this.data_list) {
					for (let item of val.myShopingSkuList) {
						if (item.sku_check) {
							myShopingSkuObj = {
								"seqId": item.seqId,
								"qty": item.qty
							}

						} else {
							continue;
						}
						myShopingSkuList.push(myShopingSkuObj)
					}
					oneShopingShopObj = {
						"merchantSeqId": val.merchantSeqId,
						"merchantShopId": val.merchantShopId,
						"logisticPayType":val.logisticPayType,
						"logisticFee":val.logisticFee,
						"myShopingSkuList": myShopingSkuList
					}
					myShopingShopList.push(oneShopingShopObj)

				}
				if (myShopingSkuList.length == 0) {
					this.$box.confirm('请先选择要购买商品')
					return
				}
				this.$api.post('/o2oShopingCart/initOrder', {
					"customerSeqId": this.$common.getLocalSync('customerSeqId'),
					"customerName": this.$common.getLocalSync('customerName'),
					"myShopingShopList": myShopingShopList
				}).then(res => {
					if (res.code == 200) {
						this.$store.commit('saveOrderConfirm', res.data)
						uni.navigateTo({
							url: '/pages/goods/order_confirm?type=ZC'
						})
					}
				})
			},
			// 获取列表
			loadList() {
				this.$api.get('/o2oShopingCart/selectMyShopingCart', {
					params: {
						customerSeqId: this.$common.getLocalSync('customerSeqId')
					}
				}).then((res) => {
					if (res.code == 200) {
						let data = res.data;
						if (data) {
							let curPageData = data.myShopingShopList;
							// 接口返回的当前页数据列表 (数组)
							for (let i in curPageData) {
								let the_myShopingSkuList = curPageData[i].myShopingSkuList;
								Vue.set(curPageData[i], 'merchant_check', false)
								for (let a in the_myShopingSkuList) {
									the_myShopingSkuList[a]
									Vue.set(the_myShopingSkuList[a], 'sku_check', false)
								}
							}
							this.data_list = curPageData;
						} else if(res.code!=401){
							this.data_list = []
						}
					}
				})
			},
			// 删除订单
			bindClick(id, spu_key, sku_index) {
				let that = this;
				uni.showModal({
					title: '提示？',
					content: '确认删除该商品？',
					success: function(res) {
						if (res.confirm) {
							that.$api.get('/o2oShopingCart/delete', {params:{
								seqId: id
							}}).then(res => {
								if (res.code == 200) {
									that.$box.toast('删除成功', 'success');
									if(that.data_list[spu_key].myShopingSkuList.length==1){
										that.data_list.splice(spu_key, 1)
									}else{
										that.data_list[spu_key].myShopingSkuList.splice(sku_index, 1)	
									}
								}
							}).catch(err => {
								console.log(err);
							})
						} else if (res.cancel) {}
					}
				});
			},
			// 获取数量
			getQty(e, k, i) {
				this.data_list[k].myShopingSkuList[i].qty = e;
				this.$api.get('/o2oShopingCart/updateCartQty',{
					params:{
						seqId:this.data_list[k].myShopingSkuList[i].seqId,
						qty:e
					}
				}).then(res=>{
				})
			},
			//点击选中门店
			checkMerchant(k) {
				let check_length = 0;
				this.data_list[k].merchant_check = !this.data_list[k].merchant_check;
				for (let val of this.data_list[k].myShopingSkuList) {
					val.sku_check = this.data_list[k].merchant_check
				}
				for (let val of this.data_list) {
					if (val.merchant_check) {
						check_length += 1
					} else {
						break
					}
				}
			},
			//点击选中商品
			checkSku(k, i) {
				let the_myShopingSkuList = this.data_list[k].myShopingSkuList[i];
				let the_check = the_myShopingSkuList.sku_check;
				let sku_check_length = 0;
				the_myShopingSkuList.sku_check = !the_check;
				for (let item of this.data_list[k].myShopingSkuList) {
					if (item.sku_check) {
						sku_check_length += 1;
					} else {
						break
					}
				}
				if (sku_check_length == this.data_list[k].myShopingSkuList.length) {
					this.data_list[k].merchant_check = true
				} else {
					this.data_list[k].merchant_check = false
				}
			},
			//点击全选
			checkAll() {
				// 没商品就不操作
				if(this.data_list.length==0){
					return
				}
				let the_check = !this.check_all;
				for (let val of this.data_list) {
					val.merchant_check = the_check;
					for (let item of val.myShopingSkuList) {
						item.sku_check = the_check;
					}
				}

			}
		},
		computed: {
			check_all() {
				try {
					if (this.data_list.length > 0) {
						let check_length = 0;
						for (let val of this.data_list) {
							if (val.merchant_check) {
								check_length += 1;
							} else {
								break
							}
						}
						if (check_length == this.data_list.length) {
							return true
						} else {
							return false
						}
					}else {
						return false
					}
				} catch (err) {
					console.log(err);
				}
			},
			all_price() {
				try {
					if (this.data_list.length > 0) {
						let all_price = 0;
						for (let val of this.data_list) {
							for (let item of val.myShopingSkuList) {
								if (item.sku_check) {
									all_price += item.effectivePrice * item.qty;
								} else {
									continue;
								}
							}
						};
						return all_price
					} else {
						return 0
					}
				} catch (err) {
					console.log(err);
				}
			}

		}
	}
</script>

<style lang="scss">
	.shop-empty {
		width: 100%;
		height: auto;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;

		.empty-icon {
			margin-top: 140rpx;
			width: 280rpx;
			height: 280rpx;
		}

		.empty-tip {
			width: 100%;
			margin-top: 10rpx;
			font-size: 24rpx;
			color: gray;
			text-align: center;
		}
	}

	.fix-box {
		bottom: var(--window-bottom);
		justify-content: space-between;

		.left {
			display: flex;
			font-size: 26rpx;
			align-items: center;

			.iconfont {
				margin-right: 20rpx;
			}
		}

		.right {
			display: flex;
			align-items: center;

			.total-price {
				font-size: 24rpx;
				color: rgba(153, 153, 153, 1);
				.tips {
					font-size: 16rpx;
					margin-right: 10rpx;
				}
			}

			.money {
				color: $color-money;
				font-size: 26rpx;
			}

			.bg-btn {
				margin-left: 30rpx;
				width: 156rpx;
				height: 74rpx;
				line-height: 74rpx;
				text-align: center;
				padding: 0;
			}
		}
	}

	.car-list {
		width: 640rpx;
		background: rgba(255, 255, 255, 1);
		border-radius: 14rpx;
		margin: 30rpx auto;
		padding: 40rpx 20rpx;

		.shop-name {
			display: flex;
			align-items: center;
			margin-bottom: 40rpx;

			.iconfont {
				font-size: 40rpx;
				color: #999;
				margin-right: 16rpx;
			}

			.name {
				font-size: 26rpx;
				font-weight: 500;
				color: rgba(16, 16, 16, 1);
				line-height: 30rpx;
			}
		}
	}

	.car-box {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 40rpx;
		.iconfont {
			font-size: 40rpx;
			width: 40rpx;
			color: #999;
			flex-shrink: 0;
		}

		.car-box-main {
			display: flex;
			justify-content: space-between;
			align-items: center;
			}

		.img {
			width: 180rpx;
			height: 180rpx;
			border-radius: 8rpx;
			margin: 0 24rpx;
			flex-shrink: 0;
		}

		.right {
			height: 180rpx;
			display: flex;
			flex-wrap: wrap;
			font-size: 30rpx;
			align-content: space-between;

			.title {
				width: 100%;
				text-overflow: -o-ellipsis-lastline;
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				line-clamp: 2;
				-webkit-box-orient: vertical;
			}

			.tips {
				font-size: 20rpx;
				color: #999;
				margin-top: 16rpx;
			}

			.price-box {
				width: 100%;
				display: flex;
				justify-content: space-between;
				align-items: center;

				.price {
					font-size: 32rpx;
					color: $color-money;
				}
			}

		}
	}

	.iconlujing {
		font-size: 50rpx !important;
	}

	.iconxuanzhong {
		color: $color-orange !important;
	}
</style>
